<template>
    <div class="guide">
      <div class="record-bar">
        <van-nav-bar
          :title="$t('language')"
          left-arrow
          @click-left="linkHome"
        />
      </div>
      <div class="guideLanguage">
<!--        <img class="guide-content" src="../../assets/guide.jpg" />-->
        <van-radio-group v-model="radio" @change="changeLanguage">
          <van-radio name="zh">简体中文</van-radio>
          <van-radio name="en">English</van-radio>
          <van-radio name="ja">日本語</van-radio>
        </van-radio-group>
      </div>
    </div>
</template>

<script>
    export default {
        name: "index",
      data(){
          return{
            radio:''
          }
      },
      created () {
        this.radio = localStorage.i18n
      },
      methods:{
        linkHome(){
          this.$router.push({
            name:'home'
          })
        },
        changeLanguage(e){
          console.log(e)
          if(e === 'zh'){
            localStorage.setItem('i18n','zh')
            this.$i18n.locale = 'zh'
          }else if(e === 'en'){
            localStorage.setItem('i18n','en')
            this.$i18n.locale = 'en'
          }else if(e === 'ja'){
            localStorage.setItem('i18n','ja')
            this.$i18n.locale = 'ja'
          }
        }
      }
    }
</script>

<style scoped lang="scss">
  @import "../../style/my";
</style>



